import React, { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { NewAppointment } from "common/components/NewAppointment";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { TeensMain } from "common/components/teens/TeensMain";
import { TeensSteps } from "common/components/teens/TeensSteps";
import { TeensClinicVisit } from "common/components/teens/TeensClinicVisit";
import { Questions } from "common/components/new/Questions";
import { Brackets } from "common/components/teens/Brackets";
import { HeaderShort } from "common/components/new/HeaderShort";
import { PriceBlock } from "common/components/new/PriceBlock";
import { TeenAdvantages } from "common/components/new/TeenAdvantages";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { Container } from "common/components/Container";
import { Heading } from "common/components/new/Heading";
import { CountryVersion } from "common/constants/enum";

import { Footer } from "components/Footer";

import { UniversalTeenLayoutProps } from "./UniversalTeenLayout.types";
import styles from "./UniversalTeenLayout.module.scss";

const cx = classNames.bind(styles);

export const UniversalTeenLayout: FC<UniversalTeenLayoutProps> = ({
    content: {
        main,
        steps,
        clinic_visit,
        questions,
        brackets,
        contacts,
        bookingFormBlock,
        prices,
    },
    version,
    language,
}) => {
    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    const handleScrollToAppointment = () => {
        scrollToAppointment(true);
    };

    return (
        <MaxWidthContainer className={cx("root")}>
            <HeaderShort
                className={cx("header")}
                button={{
                    onClick: handleScrollToAppointment,
                    name: main.ctaButton,
                }}
                isFixedButtonActive={isFixedButtonActive}
                {...contacts}
            />
            <Container className={cx("container")}>
                <TeensMain
                    className={cx("main")}
                    {...main}
                    onButtonClick={handleScrollToAppointment}
                />
                <TeensSteps className={cx("steps")} {...steps} />
            </Container>
            <Brackets {...brackets} />
            {prices && (
                <>
                    <Container className={cx("container")}>
                        <PriceBlock
                            {...prices}
                            version={version}
                            onButtonClick={handleScrollToAppointment}
                            className={cx("prices")}
                        />
                    </Container>
                    {prices.advantages && (
                        <TeenAdvantages
                            className={cx("advantages")}
                            {...prices.advantages}
                        />
                    )}
                </>
            )}
            <Container className={cx("container")}>
                <TeensClinicVisit
                    className={cx("clinic-visit")}
                    {...clinic_visit}
                    version={version}
                    onButtonClick={handleScrollToAppointment}
                />
            </Container>
            <div ref={appointmentRef}>
                <Container>
                    <Heading
                        className={cx("appointment-title", {
                            "appointment-title_ss-pro":
                                version === CountryVersion.GERMANY,
                            "appointment-title_germany":
                                version === CountryVersion.GERMANY,
                        })}
                    >
                        {bookingFormBlock.title}
                    </Heading>
                    <NewAppointment
                        version={version}
                        language={language}
                        className={cx("appointment")}
                    />
                </Container>
            </div>
            <Container className={cx("container")}>
                {questions && (
                    <Questions
                        className={cx("questions")}
                        withImages
                        {...questions}
                    />
                )}
                <Footer {...contacts} />
            </Container>
        </MaxWidthContainer>
    );
};
