import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Icon } from "common/components/Icons";
import QuoteIcon from "common/components/Icons/icons/quote.svg";
import { WINDOW_SIZE } from "common/constants/enum";

import {
    QuoteProps,
    Theme,
    QuoteAuthorSize,
    QuoteAvatarSize,
} from "./Quote.types";
import styles from "./Quote.module.scss";

const cx = classNames.bind(styles);

export const Quote: FunctionComponent<QuoteProps> = ({
    text,
    avatar,
    avatarMobile,
    author,
    className,
    smallText = false,
    theme = Theme.DEFAULT,
    avatarSize = QuoteAvatarSize.MEDIUM,
    authorSize = QuoteAuthorSize.MEDIUM,
}) => {
    return (
        <div
            className={cx("root", className, {
                "root--flexible": theme === Theme.FLEXIBLE,
            })}
        >
            {theme === Theme.FLEXIBLE ? (
                <picture>
                    <source
                        srcSet={avatar}
                        media={`(min-width: ${WINDOW_SIZE.DESKTOP}px)`}
                    />
                    <img
                        className={cx(
                            "avatar",
                            "avatar--flexible",
                            smallText && "avatar--mr"
                        )}
                        src={avatarMobile}
                        alt={author}
                    />
                </picture>
            ) : (
                <img
                    className={cx(
                        "avatar",
                        smallText && "avatar--mr",
                        `avatar_${avatarSize}`
                    )}
                    src={avatar}
                    alt={author}
                />
            )}

            <div>
                <Heading
                    tag={HeadingTag.H3}
                    type={HeadingType.S}
                    className={cx("text", {
                        "text--flexible": theme === Theme.FLEXIBLE,
                        "text--small": smallText,
                    })}
                >
                    <Icon
                        className={cx(
                            "icon",
                            theme === Theme.FLEXIBLE && "icon--flexible"
                        )}
                    >
                        <QuoteIcon />
                    </Icon>
                    &ldquo;{text}&rdquo;
                </Heading>
                <Text
                    className={cx("author", `author_${authorSize}`, {
                        "author--flexible": theme === Theme.FLEXIBLE,
                        "author--small-text": smallText,
                    })}
                >
                    {author}
                </Text>
            </div>
        </div>
    );
};
