import { HTMLAttributes } from "react";

import { TextFont } from "common/components/Text";

export enum Theme {
    DEFAULT = "default",
    FLEXIBLE = "flexible",
}

export enum QuoteAvatarSize {
    NONE = "none",
    SMALL = "small",
    MEDIUM = "medium",
}

export enum QuoteAuthorSize {
    SMALL = "small",
    MEDIUM = "medium",
}

export interface QuoteProps extends HTMLAttributes<unknown> {
    text: string;
    avatar: string;
    author: string;

    theme?: Theme;
    avatarMobile?: string;
    smallText?: boolean;
    className?: string;
    size?: string;
    avatarSize?: QuoteAvatarSize;
    authorSize?: QuoteAuthorSize;
    fontAuthor?: TextFont;
}
