import { FC } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import {
    Quote,
    QuoteAuthorSize,
    QuoteAvatarSize,
} from "common/components/new/Quote";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Picture } from "common/components/Picture";

import { TeensMainProps } from "./TeensMain.types";
import styles from "./TeensMain.module.scss";

const cx = classNames.bind(styles);

export const TeensMain: FC<TeensMainProps> = ({
    title,
    subtitle,
    description,
    quote,
    ctaButton,
    poster_big,
    poster_small,
    advantages,
    className,
    onButtonClick,
    buttonLink,
}) => {
    const renderAdvantage = (item: string, index: number) => (
        <li className={cx("advantages-item")} key={`advantages-item-${index}`}>
            {item}
        </li>
    );

    const buttonAction = onButtonClick
        ? { onClick: onButtonClick }
        : { href: buttonLink };

    return (
        <section className={cx("root", className)}>
            <div className={cx("mobile-galery")}>
                <Picture
                    className={cx("mobile-image", "mobile-image_1")}
                    mobile={poster_big.mobile}
                />
                <Picture
                    className={cx("mobile-image", "mobile-image_2")}
                    mobile={poster_small.mobile}
                />
            </div>
            <div className={cx("first-row")}>
                <div className={cx("desktop-galery")}>
                    <Picture
                        className={cx("desktop-image_1")}
                        desktop={poster_big.desktop}
                    />
                    <div className={cx("wave-background")}></div>
                </div>
                <div className={cx("content")}>
                    <Heading
                        type={HeadingType.S}
                        tag={HeadingTag.H3}
                        className={cx("subtitle")}
                    >
                        {subtitle}
                    </Heading>
                    <Heading tag={HeadingTag.H1} className={cx("title")}>
                        {title}
                    </Heading>
                    <Text className={cx("description")}>{description}</Text>
                    <Button
                        size={ButtonSize.M}
                        className={cx("only-desktop", "button")}
                        {...buttonAction}
                    >
                        {ctaButton}
                    </Button>
                </div>
            </div>
            <div className={cx("second-row")}>
                <Quote
                    className={cx("quote")}
                    avatarSize={QuoteAvatarSize.SMALL}
                    authorSize={QuoteAuthorSize.SMALL}
                    {...quote}
                />
                <Button
                    className={cx("only-mobile")}
                    size={ButtonSize.M}
                    {...buttonAction}
                >
                    {ctaButton}
                </Button>
                <div className={cx("advantages")}>
                    <Picture
                        className={cx("advantages-image")}
                        desktop={poster_small.desktop}
                    />
                    <ul className={cx("advantages-list")}>
                        {advantages.map(renderAdvantage)}
                    </ul>
                </div>
            </div>
        </section>
    );
};
